.signform {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 400px;
  width: 100%;
  margin: auto;
  padding-top: 68px;
}

.signform__logo {
}

.signform__title {
  margin-top: 40px;
}

.signform__form {
  margin-top: 23px;
}

.signform__field {
  max-width: 396px;
}

.signform__label {
  display: block;
  color: #a0a0a0;
  font-size: 10px;
  margin-top: 14px;
}

.signform__input {
  max-width: 396px;
  width: 100%;
  padding: 9px 0;
  outline: 0;
  border: 0;
  border-bottom: 1px solid #e8e8e8;
}

.signform__input:focus {
  border-bottom-color: black;
}

.signform__input_invalid {
  color: #ff4062;
  border-color: #ff4062;
}

.signform__info {
  color: #ff4062;
  font-size: 10px;
  letter-spacing: 0;
  visibility: hidden;
}

.signform__submit {
  width: 100%;
  text-align: center;
  background-color: black;
  border: 0;
  color: white;
  border-radius: 3px;
  padding: 14px 77px;
  margin: 136px auto 0;
  display: block;
  transition: opacity 0.3s;
}

.signform__link {
  color: #a0a0a0;
  text-decoration: none;
  align-self: center;
  margin-top: 16px;
  font-size: 14px;
  border-bottom: 2px solid transparent;
}

.signform__link:hover {
  border-bottom-color: black;
}

.signform__link-accent {
  color: black;
}

.signin__submit {
  margin-top: 206px;
}

.signform__submit:hover {
  opacity: 0.9;
}

@media screen and (max-width: 450px) {
  .signform {
    max-width: calc(100% - 40px);
  }

  .signform__form {
    max-width: 100%;
    margin: auto;
  }
}
