.movie {
  max-width: 364px;
  width: 100%;
  border-radius: 6px;
  overflow: hidden;
  background-color: #fdfdfd;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s ease-in-out;
  position: relative;
}

.movie__cover {
  max-width: 364px;
  width: 100%;
  height: 203px;
  object-fit: cover;
}

.movie__title {
  font-size: 13px;
  font-weight: 500;
  margin-top: 16px;
  margin-left: 16px;
  max-width: 290px;
}

.movie__button {
  float: right;
  width: 14px;
  height: 12px;
  border: 0;
  background-color: rgba(0, 0, 0, 0);
  margin-right: 24px;
  position: relative;
  top: -12px;
  transition: filter 0.2s ease-in-out;
}

.movie__button_delete {
  background-image: url("../../images/cross.svg");
  background-repeat: no-repeat;
  visibility: hidden;
}

.movie__button_like {
  background-image: url("../../images/heart.svg");
}

.movie__button_liked {
  background: url("../../images/heart-filled.svg") no-repeat center;
}

.movie__button:hover {
  filter: contrast(0%);
}

.movie:hover .movie__button_delete {
  visibility: visible;
}

.movie__duration {
  font-size: 11px;
  color: #a0a0a0;
  margin: 17px 0 16px 16px;
}

.movie:hover {
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
}

@media screen and (max-width: 768px) {
  .movie__cover {
    height: 190px;
  }

  .movie__title {
    margin-top: 10px;
  }

  .movie__duration {
    margin: 17px 0 11px 16px;
  }
}

@media screen and (max-width: 320px) {
  .movie__cover {
    height: 168px;
  }
}
